@import '../../../variable.scss';

.pop {
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    &__active {
      top: 0;
      left: 0;
      display: block;
      position: fixed;
      min-width: 100vw;
      min-height: 100vh;
      background-color: rgba($color: $gray, $alpha: 0.8);
      transition: 0.2s all;
      z-index: 1000;
    }
    &__container {
      opacity: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 100vw;
      min-height: 100vh;
    }
    &__block {
      border-radius: 9px;
      background-color: #fff;
      opacity: 1;
      padding: 20px 40px;
      box-shadow: $shadow;
    }
    &__buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 30px;
    }
    &__input {
      margin-bottom: 23px;
      display: flex;
      
     &__label{
         margin-right: 15px;
         margin:auto;
         padding-right: 15px;       
     }
     
    }
  }
  